import axios from 'axios';
import { ENV } from './../env';

class HttpService {

    HOST = (location.hostname === "localhost" || location.hostname === "127.0.0.1" ? ENV.NODE_API_SERVER_LOCAL_HOST : ENV.NODE_API_SERVER_REMOTE_HOST);
    
    sendSmsOtp(mobile) {
        const data = {
            mobile
        }
        return axios.post(this.HOST + 'otp/send', data);
    }

    retrySmsOtp(mobile) {
        const data = {
            mobile
        }
        return axios.post(this.HOST + 'otp/retry', data);
    }

    verifySmsOtp(mobile, otp) {
        const data = {
            mobile, otp
        }
        return axios.post(this.HOST + 'otp/verify', data);
    }

    addUser(user) {
        return axios.post(this.HOST + 'user', user);
    }

    getUserXls() {
        return axios.get(this.HOST + 'user/xls', {responseType: 'blob'});
    }
}
export const Http = new HttpService(); 


